@use 'base/variables' as v;
@use 'base/mixins' as m;

.about{
    background-color: v.$negro;
    margin-top: 10rem;
}

.about__h2{
    color: v.$blanco;
    font-size: 3rem;
    text-align: center;
    padding-top : 5rem;
    @include m.tablet {
        font-size: 4rem;
    }
    
}

.about__contenido{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
    @include m.telefonom {
        display: flex;
        flex-direction: column;
    }
}

.about__img{
    margin:0 auto 10rem auto;
   
   
    source,img{
        width: 100%;
        
    }
}

.about__info{
    margin: 0 3rem 10rem 0;
    border-radius: 6px;
   
    h3{
        font-size: 3rem;
        color: v.$blanco;
        text-align: center;
        margin-top: 1rem;
    }

    p{
        font-size: 2rem;
        padding: 2rem;
        color: v.$blanco;
    }
}

