@use 'base/variables' as v;
@use 'base/mixins' as m;

.contacto{
    margin: 10rem 0rem ;
}

.contacto__form{
    width: 60rem;
    min-height: 60rem;
    margin: 0 auto;
    background: #0276b5a8;
    padding: 1rem 2rem;

    @include m.telefonom {
        width: 100%;
    }
}

.contacto__h2{
    text-align: center;
    font-size: 3rem;
    @include m.telefono {
        font-size: 4rem;
    }
}

.contacto__button{
    padding: 1.5rem;

    &:hover{
        cursor: pointer;
    }
}

input, textarea{
    width: 100%;
    margin-bottom: 2rem;
    padding: 0.7rem;
    font-size: 1.6rem;
}