@charset "UTF-8";
/* (Opcional) Desactivamos los animations en el caso de que el usuario haya configurado el modo sin animation */
@media (prefers-reduced-motion: reduce) {
  * {
    animation: none !important;
    transition: none !important;
  }
}
/* Reseteamos los margin y paddings de todas las etiquetas */
*, *::before, *::after {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  vertical-align: baseline;
}

*::before, *::after {
  display: block;
}

/* Evitamos problemas con las imagenes */
img, picture, video, iframe, figure {
  max-width: 100%;
  width: 100%;
  display: block;
  /* (Opcional) */
  object-fit: cover;
  /* (Opcional) */
  object-position: center center;
}

/* Reseteamos los enlaces para funcionar como cajas... */
a {
  display: block;
  text-decoration: none;
  color: inherit;
  font-size: inherit;
}

/* ... excepto los que se encuentran en párrafos */
p a {
  display: inline;
}

/* Quitamos los puntos de los <li> */
li {
  list-style-type: none;
}

/* (Opcional) Configuramos anclas suaves */
html {
  scroll-behavior: smooth;
}

/* Desactivamos estilos por defecto de las principales etiquetas de texto */
h1, h2, h3, h4, h5, h6, p, span, a, strong, blockquote, i, b, u, em {
  font-size: inherit;
  font-weight: inherit;
  font-style: inherit;
  text-decoration: none;
  color: inherit;
}

/* Evitamos problemas con los pseudoelementos de quotes */
blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

/* (Opcional) Configuramos el texto que seleccionamos */
::selection {
  background-color: var(--negro);
  color: var(--blanco);
}

/* Reseteamos las tablas */
table, tr, td {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Evitamos problemas con los SVG */
svg {
  width: 100%;
  display: block;
  fill: currentColor;
}

/* Configuramos la tipografía para toda la web */
body {
  min-height: 100vh;
  font-size: 100%;
  font-family: var(--tipo-principal);
  color: var(--negro);
  /* (Opcional) */
  hyphens: auto;
  /* (Opcional) */
  font-smooth: always;
  /* (Opcional) */
  -webkit-font-smoothing: antialiased;
  /* (Opcional) */
  -moz-osx-font-smoothing: grayscale;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.contenedor {
  width: 98%;
  max-width: 220rem;
  margin: 0 auto;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

.header {
  background-color: #ffffff;
}
@media (min-width: 880px) {
  .header.fijo {
    width: 100%;
    position: fixed;
    z-index: 2;
  }
}
.header h1 {
  color: #000000;
  text-align: center;
  font-size: 3.4rem;
}
@media (min-width: 880px) {
  .header h1 {
    margin: 0;
  }
}
.header span {
  color: #0277b5;
}

.header__contenido {
  padding: 2rem;
}
@media (min-width: 880px) {
  .header__contenido {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 880px) {
  .header__nav {
    display: flex;
    gap: 5rem;
  }
}
.header__nav a {
  color: #000000;
  font-size: 2rem;
  display: block;
  text-align: center;
  margin-bottom: 1rem;
}
@media (min-width: 880px) {
  .header__nav a {
    margin: 0;
  }
}
.header__nav a:hover {
  color: #0277b5;
}

.header__ul {
  margin: 0;
  display: none;
}
@media (min-width: 880px) {
  .header__ul {
    display: flex;
    gap: 2rem;
  }
}
.header__ul a {
  color: #000000;
  font-size: 2rem;
  display: block;
  text-align: center;
  margin-bottom: 1rem;
}
@media (min-width: 880px) {
  .header__ul a {
    margin: 0;
  }
}
.header__ul a:hover {
  color: #0277b5;
}

.video {
  max-height: 87rem;
  overflow: hidden;
  width: 100%;
}

.portfolio {
  width: 95.5%;
  max-width: 220rem;
}

.portfolio__h2 {
  font-size: 3rem;
  text-align: center;
  margin-top: 5rem;
}
@media (min-width: 880px) {
  .portfolio__h2 {
    font-size: 4rem;
  }
}

.portfolio__galeria {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 4rem;
}
@media (max-width: 880px) {
  .portfolio__galeria {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 480px) {
  .portfolio__galeria {
    grid-template-columns: repeat(1, 1fr);
  }
}

picture {
  position: relative;
  overflow: hidden;
  border-radius: 6px;
}
picture img, picture source {
  width: 100%;
  height: 100%;
  transition: all 500ms ease-out;
}

.proyecto__capa {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #000000;
  transition: all 500ms ease-out;
  opacity: 0;
  text-align: center;
}

picture:hover > .proyecto__capa {
  opacity: 0.9;
}

picture:hover > .proyecto__capa h3 {
  font-size: 2.5rem;
  margin-top: 25%;
  margin-bottom: 5%;
  color: #0277b5;
}
@media (min-width: 880px) {
  picture:hover > .proyecto__capa h3 {
    font-size: 3.5rem;
  }
}

picture:hover > .proyecto__capa a {
  display: inline-block;
  font-size: 2rem;
  color: #0277b5;
  margin: 1rem;
  font-weight: 600;
  padding: 1rem;
  border-radius: 6px;
  background-color: #ffffff;
}
@media (min-width: 880px) {
  picture:hover > .proyecto__capa a {
    font-size: 3rem;
  }
}

.about {
  background-color: #000000;
  margin-top: 10rem;
}

.about__h2 {
  color: #ffffff;
  font-size: 3rem;
  text-align: center;
  padding-top: 5rem;
}
@media (min-width: 880px) {
  .about__h2 {
    font-size: 4rem;
  }
}

.about__contenido {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 4rem;
}
@media (max-width: 480px) {
  .about__contenido {
    display: flex;
    flex-direction: column;
  }
}

.about__img {
  margin: 0 auto 10rem auto;
}
.about__img source, .about__img img {
  width: 100%;
}

.about__info {
  margin: 0 3rem 10rem 0;
  border-radius: 6px;
}
.about__info h3 {
  font-size: 3rem;
  color: #ffffff;
  text-align: center;
  margin-top: 1rem;
}
.about__info p {
  font-size: 2rem;
  padding: 2rem;
  color: #ffffff;
}

.contacto {
  margin: 10rem 0rem;
}

.contacto__form {
  width: 60rem;
  min-height: 60rem;
  margin: 0 auto;
  background: rgba(2, 118, 181, 0.6588235294);
  padding: 1rem 2rem;
}
@media (max-width: 480px) {
  .contacto__form {
    width: 100%;
  }
}

.contacto__h2 {
  text-align: center;
  font-size: 3rem;
}
@media (min-width: 480px) {
  .contacto__h2 {
    font-size: 4rem;
  }
}

.contacto__button {
  padding: 1.5rem;
}
.contacto__button:hover {
  cursor: pointer;
}

input, textarea {
  width: 100%;
  margin-bottom: 2rem;
  padding: 0.7rem;
  font-size: 1.6rem;
}

footer {
  background-color: #000000;
  height: 9rem;
}

.footer__p {
  color: #ffffff;
  padding: 3rem;
  text-align: center;
  font-size: 2rem;
}