@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    background-color: v.$blanco;

    @include m.tablet {
        &.fijo{
            width: 100%;
            position: fixed;
            z-index: 2;
        }
    }
       
    
    

    h1{
        color: v.$negro;
        text-align: center;
        @include m.tablet {
            margin:0;
        }
        font-size: 3.4rem;
       
    }

    span{
        color: v.$azul;
    }
}

.header__contenido{
    padding: 2rem;

    @include m.tablet {
        display:flex;
        justify-content: space-between;
        align-items: center;
    }
}


.header__nav{
    @include m.tablet {
        display: flex;
        gap: 5rem;
    }

    a{
        color: v.$negro;
        font-size: 2rem;
        display: block;
        text-align: center;
        margin-bottom: 1rem;
        @include m.tablet {
            margin: 0;
        }

        &:hover{
            color: v.$azul;
        }
    }
}

.header__ul{
    margin: 0;
    display: none;

    @include m.tablet {
        display: flex;
        gap: 2rem;
    }

    a{
        color: v.$negro;
        font-size: 2rem;
        display: block;
        text-align: center;
        margin-bottom: 1rem;
        @include m.tablet {
            margin: 0;
        }

        &:hover{
            color: v.$azul;
        }
    }
}