@use 'base/variables' as v;
@use 'base/mixins' as m;

.portfolio{
    width: 95.5%;
    max-width: 220rem;
    
}

.portfolio__h2{
    font-size: 3rem;
    text-align: center;
    margin-top: 5rem;
    @include m.tablet {
        font-size:4rem;
    }
}

.portfolio__galeria{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
    @include m.tabletm {
        grid-template-columns: repeat(2, 1fr);
    }
    @include m.telefonom {
        grid-template-columns: repeat(1, 1fr);
    }
}



picture{
    position: relative;
    overflow: hidden;
    border-radius: 6px;
    img, source{
        width: 100%;
        height: 100%;
        transition: all 500ms ease-out;
    }

}

.proyecto__capa{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: v.$negro;
    transition: all 500ms ease-out;
    opacity: 0;
    text-align: center;
}

picture:hover > .proyecto__capa{
    opacity: 0.9;
}

picture:hover > .proyecto__capa h3{
    font-size: 2.5rem;
    margin-top: 25%;
    margin-bottom: 5%;
    color: v.$azul;
    @include m.tablet {
        font-size: 3.5rem;
    }

    
}

picture:hover > .proyecto__capa a{
    display: inline-block;
    font-size: 2rem;
    color: v.$azul;
    margin: 1rem;
    font-weight: 600;
    padding: 1rem;
    border-radius: 6px;
    background-color: v.$blanco;
    @include m.tablet {
        font-size: 3rem;
    }
}